import './app-view.css';

export default class AppView {
  constructor(data) {
    this.data = data;
  }

  render() {
    const main = document.createElement('main');
    const dataWrupper = document.createElement('div');
    const dataNavigation = document.createElement('div');
    const navigationItemPrevious = document.createElement('div');
    const navigationItemCurrent = document.createElement('div');
    const navigationItemNext = document.createElement('div');
    main.className = 'main';
    dataWrupper.className = 'data-wrupper';
    dataWrupper.id = 'data-wrupper';
    dataNavigation.className = 'data-navigation';
    navigationItemPrevious.className = 'data-navigation__previous';
    navigationItemPrevious.id = 'data-navigation__previous';
    navigationItemPrevious.style.visibility = 'hidden';
    navigationItemCurrent.className = 'data-navigation__current';
    navigationItemCurrent.id = 'data-navigation__current';
    navigationItemCurrent.innerText = '1';
    navigationItemNext.innerText = '2';
    navigationItemNext.className = 'data-navigation__next';
    navigationItemNext.id = 'data-navigation__next';
    dataNavigation.appendChild(navigationItemPrevious);
    dataNavigation.appendChild(navigationItemCurrent);
    dataNavigation.appendChild(navigationItemNext);
    document.body.appendChild(main);
    main.appendChild(dataWrupper);
    if (this.data) {
      AppView.renderClips(this.data);
      main.appendChild(dataNavigation);
    }
  }

  static removeMain() {
    document.body.removeChild(document.getElementsByTagName('main')[0]);
  }

  static createNewItem(clip) {
    const newElement = document.createElement('div');
    const imgClip = document.createElement('img');
    const link = document.createElement('a');
    const title = document.createElement('p');
    const autorContainer = document.createElement('div');
    const autorIcon = document.createElement('img');
    const autor = document.createElement('p');
    const dateContainer = document.createElement('div');
    const dateIcon = document.createElement('img');
    const date = document.createElement('p');
    const viewCountContainer = document.createElement('div');
    const viewCountIcon = document.createElement('img');
    const viewCount = document.createElement('p');
    const description = document.createElement('p');
    newElement.className = 'data-wrupper__item';
    imgClip.className = 'data-wrupper__item-img';
    imgClip.src = clip.imgUrl;
    title.className = 'data-wrupper__item-title';
    title.innerHTML = clip.title;
    link.className = 'data-wrupper__item-link';
    link.href = clip.link;
    link.target = 'blank';
    link.appendChild(title);
    autorContainer.className = 'data-wrupper__item-container';
    autorIcon.className = 'data-wrupper__item-icon';
    autor.className = 'data-wrupper__item-p';
    autorIcon.src = '/src/views/assets/image/autor.png';
    autor.innerHTML = clip.autor;
    autorContainer.appendChild(autorIcon);
    autorContainer.appendChild(autor);
    dateContainer.className = 'data-wrupper__item-container';
    dateIcon.className = 'data-wrupper__item-icon';
    date.className = 'data-wrupper__item-p';
    dateIcon.src = '/src/views/assets/image/date.png';
    date.innerHTML = clip.upload;
    dateContainer.appendChild(dateIcon);
    dateContainer.appendChild(date);
    viewCountContainer.className = 'data-wrupper__item-container';
    viewCountIcon.className = 'data-wrupper__item-icon';
    viewCount.className = 'data-wrupper__item-p';
    viewCountIcon.src = '/src/views/assets/image/views.png';
    viewCount.innerHTML = clip.viewCount;
    viewCountContainer.appendChild(viewCountIcon);
    viewCountContainer.appendChild(viewCount);
    description.className = 'data-wrupper__item-description';
    description.innerHTML = clip.description;
    newElement.appendChild(imgClip);
    newElement.appendChild(link);
    newElement.appendChild(autorContainer);
    newElement.appendChild(dateContainer);
    newElement.appendChild(viewCountContainer);
    newElement.appendChild(description);

    return newElement;
  }

  static renderSearchBox() {
    const header = document.createElement('header');
    const searchContainer = document.createElement('div');
    const searchBox = document.createElement('input');
    const searchImg = document.createElement('div');
    header.className = 'header';
    searchContainer.className = 'search-container';
    searchBox.className = 'search-container__searchbox';
    searchBox.id = 'searchbox';
    searchBox.type = 'text';
    searchBox.value = 'javascript';
    searchImg.className = 'search-container__img';
    document.body.appendChild(header);
    header.appendChild(searchContainer);
    searchContainer.appendChild(searchImg);
    searchContainer.appendChild(searchBox);
  }

  static renderClips(data) {
    const dataWrupper = document.getElementById('data-wrupper');
    data.forEach((item) => {
      dataWrupper.appendChild(AppView.createNewItem(item));
    });
  }
}
