import AppModel from '../models/AppModel';
import AppView from '../views/AppView/AppView';

export default class App {
  constructor() {
    this.state = {
      apiKey: 'AIzaSyDnTiq9jpA8ql-XVtoSHJ0KxNCrf1xQoow',
      maxResults: 15,
      searchWords: 'javascript',
    };
    this.countClipsOnPage = 0;
    this.dataWrupper = null;
    this.currentPage = 0;
    this.currentClip = 0;
    this.navPre = null;
    this.navCurent = null;
    this.navNext = null;
    this.searchBox = null;
    this.dataLength = 0;
    this.model = null;
    this.view = null;
    this.dataLength = 15;
  }

  static preloadSearchBox() {
    AppView.renderSearchBox();
  }

  async start() {
    this.model = new AppModel(this.state);
    const data = await this.model.getData();
    this.view = new AppView(data);
    await this.view.render();
    this.dataLength = data.length;
    this.changeMediaStatePages();
    this.startNavigateClip();
    this.addSearchEvent();
    this.startSlider();
  }

  startSlider() {
    let x0 = null;
    this.dataWrupper.onselectstart = () => false;

    function unify(e) { return e.changedTouches ? e.changedTouches[0] : e; }

    function lock(e) { x0 = unify(e).clientX; }

    function move(e) {
      if (x0 || x0 === 0) {
        const dx = unify(e).clientX - x0;
        const s = Math.sign(dx);
        if (s < 0) {
          this.changePage('next');
        }
        if (s > 0) {
          if (this.currentPage > 0) {
            this.changePage('pre');
          }
        }

        x0 = null;
      }
    }

    this.dataWrupper.addEventListener('mousedown', lock.bind(this), false);
    this.dataWrupper.addEventListener('touchstart', lock.bind(this), false);

    this.dataWrupper.addEventListener('mouseup', move.bind(this), false);
    this.dataWrupper.addEventListener('touchend', move.bind(this), false);
  }

  addSearchEvent() {
    this.searchBox = document.getElementById('searchbox');
    this.searchBox.onkeypress = this.sendQuery.bind(this);
  }

  sendQuery(event) {
    if (event.keyCode === 13) {
      AppView.removeMain();
      this.state.searchWords = this.searchBox.value;
      this.countClipsOnPage = 0;
      this.dataWrupper = '';
      this.currentPage = 0;
      this.currentClip = 0;
      this.navPre = null;
      this.navCurent = null;
      this.navNext = null;
      this.searchBox = null;
      this.dataLength = 15;
      this.start();
    }
  }

  startNavigateClip() {
    this.navPre = document.getElementById('data-navigation__previous');
    this.navCurent = document.getElementById('data-navigation__current');
    this.navNext = document.getElementById('data-navigation__next');
    this.navPre.addEventListener('click', this.changePage.bind(this, 'pre'));
    this.navNext.addEventListener('click', this.changePage.bind(this, 'next'));
  }

  changePage(state) {
    if (state === 'pre') this.currentPage -= 1;
    if (state === 'next') this.currentPage += 1;
    this.dataWrupper.style.setProperty('--page', this.currentPage);
    if (this.currentPage === 0) this.navPre.style.visibility = 'hidden';
    else this.navPre.style.visibility = 'visible';
    this.navPre.innerText = this.currentPage;
    this.navCurent.innerText = this.currentPage + 1;
    this.navNext.innerText = this.currentPage + 2;
    if ((this.currentPage + 2) * this.countClipsOnPage >= this.dataLength) {
      this.loadNextClips();
    }
  }

  async loadNextClips() {
    this.dataLength += 15;
    const data = await this.model.getNextData();
    await AppView.renderClips(data);
  }

  changeMediaStatePages() {
    this.dataWrupper = document.getElementById('data-wrupper');
    this.countClipsOnPage = getComputedStyle(this.dataWrupper).getPropertyValue('--count-clip-on-page');
    this.currentPage = +getComputedStyle(this.dataWrupper).getPropertyValue('--page');
    this.currentClip = this.currentPage * this.countClipsOnPage + 1;
    const mediaWidthThreeClips = window.matchMedia('only screen and (max-width: 1023px)');
    const mediaWidthTwoClips = window.matchMedia('only screen and (max-width: 767px)');
    const mediaWidthOneClips = window.matchMedia('only screen and (max-width: 511px)');
    mediaWidthThreeClips.addListener(this.changeCssVars.bind(this));
    mediaWidthTwoClips.addListener(this.changeCssVars.bind(this));
    mediaWidthOneClips.addListener(this.changeCssVars.bind(this));
  }

  changeCssVars() {
    const newCountClipsOnPage = getComputedStyle(this.dataWrupper).getPropertyValue('--count-clip-on-page');
    const newPage = Math.ceil(this.currentClip / newCountClipsOnPage) - 1;
    this.dataWrupper.style.setProperty('--page', newPage);
    this.currentPage = newPage;
    this.countClipsOnPage = newCountClipsOnPage;
  }
}
