export default class AppModel {
  constructor(state) {
    this.state = state;
    this.data = [];
    this.nextPageToken = null;
    this.baseUrl = `https://www.googleapis.com/youtube/v3/search?key=${this.state.apiKey}&type=video&part=snippet&maxResults=${this.state.maxResults}&q=${this.state.searchWords}`;
    this.url = this.baseUrl;
  }

  extractNextPageToken(data) {
    this.nextPageToken = data.nextPageToken;
  }

  extractData(data) {
    data.items.forEach((clip) => {
      const newItem = {};
      const { title } = clip.snippet;
      const { channelTitle } = clip.snippet;
      const { description } = clip.snippet;
      newItem.title = (title.length < 52) ? title : title.slice(0, 52).concat('...');
      newItem.imgUrl = clip.snippet.thumbnails.medium.url;
      newItem.id = clip.id;
      newItem.link = `https://www.youtube.com/watch?v=${newItem.id}`;
      newItem.autor = (channelTitle.length < 18) ? channelTitle : channelTitle.slice(0, 18).concat('...');
      newItem.description = (description.length < 115) ? description : description.slice(0, 115).concat('...');
      newItem.upload = clip.snippet.publishedAt.slice(0, 10);
      newItem.viewCount = clip.statistics.viewCount;
      this.data.push(newItem);
    });
  }

  async getData() {
    const responce = await fetch(this.url);
    const preData = await responce.json();
    this.extractNextPageToken(preData);
    const idClips = preData.items.map(clip => clip.id.videoId).join(',');
    const urlStatistics = `https://www.googleapis.com/youtube/v3/videos?key=${this.state.apiKey}&id=${idClips}&part=snippet,statistics`;
    const responceDataStats = await fetch(urlStatistics);
    const apiData = await responceDataStats.json();
    this.extractData(apiData);

    return this.data;
  }

  async getNextData() {
    this.url = `${this.baseUrl}&pageToken=${this.nextPageToken}`;
    const newData = await this.getData();
    return newData.slice(-15);
  }
}
